<template>
  <div class="login df aic jcc">
    <el-form
      :rules="rules"
      :model="form"
      ref="ruleForm"
      hide-required-asterisk
      class="plr40 br10 login-form"
    >
      <h3>后台系统</h3>

      <el-row>
        <el-col :span="5" class="df jcfe aic h36">
          <span>手机号：</span>
        </el-col>
        <el-col :span="19">
          <el-form-item prop="uid">
            <el-input
              maxlength="11"
              autofocus
              clearable
              v-model="form.uid"
              type="text"
              prefix-icon="el-icon-user-solid"
              placeholder="请输入手机号"
              @keyup.enter.native="handleLogin"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="5" class="df jcfe aic h36">
          <span>密码：</span>
        </el-col>
        <el-col :span="19">
          <el-form-item prop="password">
            <el-input
              maxlength="6"
              v-model="form.password"
              prefix-icon="el-icon-lock"
              placeholder="请输入身份证后六位"
              show-password
              @keyup.enter.native="handleLogin"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="5" class="df jcfe aic h36">
          <span>验证码：</span>
        </el-col>
        <el-col :span="11">
          <el-form-item prop="code">
            <el-input
              maxlength="6"
              placeholder="请输入验证码"
              @keyup.enter.native="handleLogin"
              v-model="form.code"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8" class="df jcfe">
          <el-button
            class="w20"
            type="primary"
            :disabled="form.uid.length != 11 || typeof(code_text) == 'number'"
            @click="get_code"
            v-model="code_text"
          >{{code_text}}</el-button>
        </el-col>
      </el-row>
      <el-row class="df jcc">
        <el-col :span="20">
          <el-form-item>
            <el-button
              class="df aic jcc login-button"
              type="primary"
              @click.native.prevent="handleLogin"
            >
              <span>登录</span>
            </el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
// import Cookies from "js-cookie";
export default {
  name: "login",
  data() {
    return {
      code_text: "获取验证码",
      form: {
        uid: "",
        password: "",
        code: "" //验证码
      },
      rules: {
        uid: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "请输入正确的手机号码", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入身份证后六位", trigger: "blur" },
          { min: 6, max: 6, message: "长度为 6 位字符", trigger: "blur" }
        ],
        code: [
          { required: true, message: "请输入验证码", trigger: "blur" },
          { min: 6, max: 6, message: "请输入验证码", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //登录功能
    login() {
      const loading = this.$loading({
        lock: true,
        text: "拼命加载中...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.8)"
      });
      this.$get(
        "user/hingeUserLogin",
        {
          phone: this.form.uid,
          identityCard: this.form.password,
          verificationCode: this.form.code,
          loginType: "Code" //"Password"
        },
      )
        .then(res => {
          if (!res.jwtToken) {
            this.$notify({
              title: "提示",
              message: res,
              type: "warning"
            });
            loading.close();
            return;
          }
          localStorage.setItem("userInfo", JSON.stringify(res));
          //sessionStorage.setItem("uid", this.form.uid);
          loading.close();
          this.$router.replace("/home");
        })
        .catch(res => {
          loading.close();
        });
    },
    //点击登录时候触发
    handleLogin() {
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.login();
        } else {
          return valid;
        }
      });
    },
    //点击获取验证码触发
    get_code() {
      this.$get("user/loginCode", {
        phone: this.form.uid
      }).then(res => {
        if (typeof this.code_text == "string") this.code_text = 30;
        var code_time = setInterval(() => {
          if (typeof this.code_text == "number" && this.code_text == 0) {
            this.code_text = "获取验证码";
            clearInterval(code_time);
          } else if (typeof this.code_text == "number" && this.code_text != 0) {
            this.code_text = this.code_text - 1;
          }
        }, 1000);
      });
    }
  }
};
</script>

<style scoped>
span {
  color: #606266;
}
.login {
  background: url(../../assets/img/login.jpg) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
}
.login-form {
  background: #fff;
  width: 400px;
}
.login-form > h3 {
  text-align: center;
}
.login-button {
  width: 100%;
}
</style>


